.btn {
  display: inline-block;
  box-sizing: border-box;
  position: relative;
  z-index: auto;
  background: none;
  border: 1px solid;
  color: inherit;
  height: 48px;
  padding: 0 15px;
  text-align: center;
  font-size: 18px;
  font-size: 1.8rem;
  letter-spacing: 0.02em;
  line-height: 46px;
  font-weight: 700;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  user-select: none;
  appearance: none;
}

.btn > span {
  position: relative;
  z-index: 1;
}

.btn:before {
  content: "";
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: none;
  transform: scale(0.5);
  opacity: 0;
  visibility: hidden;
  box-shadow: 0 5px 35px rgba(255, 0, 0, 0.4);
  transition: all 0.3s ease;
}

.btn:hover, .btn:focus {
  border-color: #ff0000;
  color: white;
}

.btn:hover:before, .btn:focus:before {
  opacity: 1;
  visibility: visible;
  background: #ff0000;
  transform: scale(1);
}

.btn:active {
  border-color: #f10000;
  color: white;
}

.btn:active:before {
  background: #f10000;
}

.btn.is-active {
  border-color: black;
  color: white;
}

.btn.is-active:before {
  background: black;
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}

.btn[disabled], .btn.is-disabled {
  background: none;
  color: black;
  border-color: black;
  opacity: 0.2;
  cursor: not-allowed;
}

.btn[disabled]:before, .btn.is-disabled:before {
  display: none;
}

