  padding: 7px 20px;
  box-sizing: border-box;
  background: white;
  white-space: nowrap;
  transition: all 0.2s ease;
  transform-origin: center bottom;
}

.tooltip:before {
  content: "";
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 10px 14px 0 0;
  border-color: white transparent transparent transparent;
  position: absolute;
  top: 100%;
  left: 50%;
}

.tooltip_arr-right {
  transform-origin: right bottom;
}

.tooltip_arr-right:before {
  left: auto;
  right: 0;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 14px 10px 0;
  border-color: transparent white transparent transparent;
}

.tooltip_arr-left {
  transform-origin: left bottom;
}

.tooltip_arr-left:before {
  left: 0;
}

.tile {
  display: block;
  position: relative;
  z-index: 0;
  float: left;
  width: 25%;
  box-sizing: border-box;
  background-size: cover;
  background-position: center;
  color: white;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
}

.tile:before {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.tile:after {
  content: "";
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 0, 0, 0.8);
  box-shadow: 0 0 50px rgba(255, 0, 0, 0.6);
  pointer-events: none;
  transition: all 0.3s ease;
  opacity: 0;
  visibility: hidden;
  transform: translate3d(0, 0, 0) scale(1.15);
}

.tile:hover {
  z-index: 1;
}

.tile:hover:after {
  transition-delay: 0.1s;
  opacity: 1;
  visibility: visible;
  transform: translate3d(0, 0, 0) scale(1);
}

.tile:hover .tile__play-button {
  animation: zoom-in 0.3s ease 1;
}

