.parallax-logotype {
  position: absolute;
  z-index: 0;
  top: 600px;
  right: -5px;
  height: 1003px;
  width: 212px;
  font-size: 212px;
  .icon-rezult-logotype {
    position: absolute;
    top: 0;
    right: 100%;
    fill: $color-red;
    stroke: $color-red;
    transform: rotate(-90deg);
    transform-origin: right top; } }

.promo-text {
  margin: 145px 0 235px 0;
  max-width: 800px; }

.promo-text__item {
  margin: 70px 0;
  &:first-child {
    margin-top: 0; }
  &:last-child {
    margin-bottom: 0; } }

.gallery {
  display: table;
  table-layout: fixed;
  border-spacing: 0;
  width: 100%;
  height: 100vh;
  max-height: 1000px;
  min-height: 500px; }

.gallery__left,
.gallery__right {
  display: table-cell;
  vertical-align: middle;
  width: 50%;
  position: relative; }

.gallery__left {
  overflow: hidden;
  > img {
    display: block; } }

.gallery__right {
  padding-left: 130px; }

.gallery__text-container {
  display: inline-block;
  vertical-align: middle;
  text-align: left; }

.gallery__text {
  max-width: 400px;
  @include f(16,36);
  .h2 {
    margin-bottom: 40px;
    line-height: 1.2; } }

.products {
  margin: 170px 0 0; }

.products__footer {
  text-align: center;
  margin-top: 40px; }


.section {
  margin-top: 110px; }

.section-filters {
  margin: 0 0 40px;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.section-filters__cell {
  width: 33.3%;
  text-align: center;
  &:first-child {
    text-align: left; }
  &:last-child {
    text-align: right; } }

.section-header {
  text-align: center;
  margin-bottom: 32px; }

.section-header__list {
  margin-top: 20px; }

.section-header__list-item {
  display: inline-block;
  &:not(:last-child) {
    margin-right: 25px; } }

.section-header__link {
  display: inline-block;
  position: relative;
  @include f(16,28,100);
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  color: rgba(black, 0.4);
  @include trans;
  &:hover {
    color: $color-red; }
  &.is-active {
    color: inherit;
    &:before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      border-top: 1px solid $color-red; } } }

.map {
  margin: 115px 0 0; }

.map__container {
  height: 520px;
  background: img-url('map.jpg') no-repeat center;
  background-size: cover; }


.toparea {
  height: 700px;
  box-sizing: border-box;
  padding-top: 23px;
  background: img-url('toparea.jpg') no-repeat center;
  background-size: cover;
  color: white;
  text-align: center;
  .btn {
    opacity: 0.3;
    &:hover {
      opacity: 1; } } }

.toparea__title {
  margin: 16px 0;
  @include f(64,64);
  font-weight: bold; }

.toparea__small-subtitle {
  display: block;
  margin-bottom: 4px;
  @include f(14);
  opacity: 0.5; }

.toparea__subtitle {
  @include f(16,false,100);
  font-weight: bold;
  text-transform: uppercase; }

.group-title {
  display: block;
  margin-bottom: 10px;
  @include f(14); }

.select-buttons-group {
  margin-bottom: 50px;
  font-size: 0;
  .btn {
    vertical-align: middle;
    margin: 0 20px 20px 0; } }

.figure {
  margin: 50px 0 70px;
  &_with-aside {
    font-size: 0;
    .figure__img {
      display: inline-block;
      vertical-align: middle;
      width: percentage(580/780); } } }

.figure__img {
  margin-left: -100px;
  box-sizing: border-box;
  img {
    display: block;
    max-width: 100%; } }

.figure__aside {
  display: inline-block;
  vertical-align: middle;
  width: percentage(300/780);
  box-sizing: border-box;
  text-align: center;
  padding: 0 0 0 30px;
  &:first-child {
    margin-left: -100px;
    padding: 0 30px 0 0; }
  + .figure__img {
    margin-left: 0; } }

.figure__caption {
  margin-top: 70px;
  font-size: 16px; }


.number {
  position: relative;
  text-transform: uppercase;
  @include f(16, 24, 100);
  font-weight: 700;
  &:not(:first-child) {
    margin-top: 65px; }
  &_small {
    .number__value {
      font-size: 60px; } } }

.number__value {
  display: block;
  font-size: 90px;
  line-height: 1;
  letter-spacing: 0; }


.v-line-container {
  position: relative;
  .l {
    position: relative;
    z-index: 1; } }

.v-line {
  position: absolute;
  z-index: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 50%;
  margin-left: 310px;
  background: red;
  color: white;
  overflow: hidden;
  .logotype-full {
    position: absolute;
    right: 100%;
    margin-right: percentage(-100/400);
    top: 200px;
    transform-origin: right top;
    transform: rotate(-90deg);
    font-size: 60px; } }

.step {
  position: relative;
  margin: 180px 0;
  @include f(16,24);
  &:last-child {
    margin-bottom: 0; }
  p {
    margin: 1em 0; }
  &.is-active {
    .step__number {
      color: red;
      text-shadow: 0 10px 30px rgba(red, 0.5); } } }

.step__text {
  position: relative;
  width: percentage(355/780);
  box-sizing: border-box; }

.step__img {
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
  width: percentage(415/780);
  box-sizing: border-box;
  img {
    display: block;
    max-width: 100%;
    &[src*="step-1"] {
      max-width: 115%; } } }

.step__number {
  position: absolute;
  z-index: -1;
  top: -67px;
  left: -48px;
  min-width: 80px;
  text-align: center;
  @include f(140,140);
  font-weight: 700;
  color: $color-global-bg;
  text-shadow: 1px 1px 0 red, -1px -1px 0 red, -1px 1px 0 red, 1px -1px 0 red; }
